<template>
  <div class="our">
    <mt-field label="" placeholder="" v-model="username"></mt-field>
    <mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
    <mt-field label="qq" placeholder="qq" type="number" v-model="qq"></mt-field>
    <mt-radio
      title=""
      v-model="course"
      :options="['', '', '']">
    </mt-radio>
    {{arry}}
    <mt-button @click="sub" type="danger"></mt-button>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        username:'',
        phone:'',
        qq:'',
        course:'',
        arry:[]
      }
    },
    methods:{
      sub(){
        if(this.username && this.phone && this.qq && this.course){
          this.arry.unshift({ username:this.username,phone:this.phone,qq:this.qq,course:this.course})
          MessageBox.alert('').then(action => {
            this.$router.push('/home')
          });
        }else{
          MessageBox.alert('')
        }

      }
    }
  }
</script>
<style>


</style>



